.bodyPc {
  display: grid;
  grid-template-columns: 25% 50% 25%;
  grid-template-rows: 50% 50%;
}
.inputItems {
  margin: 2rem auto;
  input {
    background-color: transparent;
    outline: 0;
    border: 0;

    letter-spacing: 1.6px;
    padding: 0 0.3rem;
    color: rgb(55, 248, 213);
    font-size: 1.1rem;
    &::placeholder {
      color: rgb(58, 234, 178);
      letter-spacing: 1.5px;
      font-size: 0.9rem;
    }
  }
  .inputStick {
    margin-top: 0.2rem;
    background: linear-gradient(#11998e, #38ef7d);
    height: 3px;
    box-shadow: 0 0 2rem 0.2rem #1dffec;
    width: 120%;
    border-radius: 1rem;
  }
  .autocompleteWeather {
    max-width: 120%;
    min-width: 115%;
    max-height: 31vh;
    background-color: rgba(98, 98, 98, 0.504);
    overflow: hidden;
    backdrop-filter: blur(0.3rem);
    border-radius: 2%;
    p {
      padding: 0.3rem;
      letter-spacing: 0.9px;
      border-bottom: 1px solid rgb(50, 255, 166);
      cursor: pointer;
      &:hover {
        color: #1dffec;
      }
      &::first-letter {
        color: #1dffec;
        font-size: 1.05rem;
      }
    }
  }
}
.bodyAll {
  grid-row: 1/3;
  grid-column: 2;
  display: flex;
  justify-content: space-around;
  align-items: center;
  span {
    display: flex;
    font-size: 1.9rem;
    color: #15ffec;
    align-items: center;
    &::first-letter{
        font-size: 2rem;
        color: #15ffec;
    }
    &:hover {
      color: #36fffc;
    
    }
  }
  h5 {margin-top: 2vh;
    color: #20ff93;
    letter-spacing: 1.5px;
    font-size: 1.1rem;
  }
}
.bodyDisplay {
  display: flex;
  flex-direction: column;
 min-width: 90%;
  align-items: center;
  justify-content: center;
  .txt {
    margin-bottom: 2.5rem;
  }
  h1 {
    color: #1dffec;
    font-size: 4.5rem;
  }
  h2 {
    letter-spacing: 1px;
    &::first-letter {
      color: #1dffec;
      font-size: 2.5rem;
    }
  }
  .bodyImg {
    img {
      max-width: 15vw;
    }
  }
}
@media (orientation: portrait){
    .bodyPc{
        display:none;
        
    }
}
